<template lang="html">
    <input type="text" :value="value" @input="input">
</template>

<script>
export default {
  props: ['value'],
  methods: {
    input(event) {
      this.$emit('input', event.target.value)
    }
  }
}
</script>

<style lang="stylus" scoped>
input
  border-bottom: 2px solid black
  height: 3em;
  text-align: right;
  width: 100%
  outline: none;
  font-size: 150%
</style>
