<template lang="html">
  <div class="input-wrapper" :class="{filled:filled}">
    <input class="input" @input="input" @keyup="keyup" :value="value" :readonly="readonly" :type="type">
    <label class="label" for="input">
      <span class="placeholder">{{placeholder}}</span>
    </label>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    readonly: {
      default: false
    },
    type: {
      type: String,
      default: 'text'
    }
  },
  methods: {
    input(event){
      this.$emit('input', event.target.value);
    },
    keyup(event){
      this.$emit('keyup',event);
    }
  },
  computed: {
    filled(){
      return this.value.length != 0;
    }
  }
}
</script>

<style lang="stylus" scoped>
.input-wrapper
  position relative
.input
  display block
  margin 0
  width 100%
  background transparent
  color: inherit;
.input:focus + .label .placeholder,
.filled .placeholder
  opacity: 0
  transform: translate(20px)
.input[readonly] + .label::after,
.input[readonly] + .label::before
  opacity: 0
.label
  display block
  margin: 0
  position: absolute
  top: 0
  left: 0
  width: 100%
  height: 100%
  padding: inherit;
  pointer-events: none
  &::after
    content: '';
    z-index: -1;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    max-height: 100%
    background: rgba(0,0,0,0.1);
    transition: all 0.3s;
  &::before
    content ''
    z-index 0
    position: absolute;
    bottom 0
    width 100%
    max-width: 0
    height 4px
    background-color rgba(0,0,0,0.4)
    transition max-width 0.3s;
.placeholder
  display: inline-block
  transition: all 0.3s
  color: inherit

.input:focus + .label::after,
.filled .label::after
  max-height: 4px

.input:focus + .label::before
  max-width: 100%

</style>
