<template lang="html">
  <div class="wrapper">
    <h1 class="background-text">Forum</h1>
    <!-- the Sections -->
      <transition name="fade" mode="out-in">
        <!-- the Sections -->
        <div class="forum">
          <div class="section">
            <div v-for="(section, title) of sections" :key="title">
              <div class="forum-section">
                <h1 class="title" v-if="title">{{title | capitalize}}</h1>
                <p class="text" v-if="section.description">{{section.description}}</p>
                <div class="subsections" v-if="section.subcategories">
                  <div class="subsection" v-for="(subcategory, subtitle) of section.subcategories" :key="subtitle">
                    <router-link class="no-highlight" :to="'/Forum/'+ (subtitle.charAt(0).toUpperCase() + subtitle.slice(1))">
                      <h2 class="title" v-if="subtitle">{{subtitle | capitalize}}</h2>
                      <p class="text" v-if="subcategory.description">{{subcategory.description}}</p>
                    </router-link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </transition>
  </div>
</template>

<script>
import firebase from 'firebase/app'
import 'firebase/database'
export default {
  data() {
    return {
      sections: {}
    }
  },
  filters: {
    capitalize: function(value) {
      if (!value) return ''
      value = value.toString()
      return value.charAt(0).toUpperCase() + value.slice(1)
    }
  },
  created() {
    let database = firebase.database();
    database.ref("/forum/subforums").once('value', snapshot => {
      this.sections = snapshot.val();
    })
  }
}
</script>

