.section
  h1, .h1, .h1 *
    font-size: calc(2vw+30px)
  h2, .h2, .h2 *
    font-size calc(0.75vw+15px)
.subsections
  display flex
  flex-wrap wrap

@media only screen and (max-aspect-ratio: 5/6)//portrait
  .subsection
    min-width: 100%
@media only screen and (min-aspect-ratio: 5/6)//landscape
  .subsection
    min-width: 400px

.subsection
  flex 1 1 0
  margin-right 3vw
  &:last-child
    margin-right 0
.no-highlight:hover
  color: inherit
.forum-section
  margin-bottom: 40px
</style>
