<template lang="html">
  <div class="wrapper">
    <h1 class="background-text">Forum</h1>
    <!-- the Sections -->
      <transition name="fade" mode="out-in">
        <!-- the Sections -->
        <div class="section">
          <div class="forum-navigation" @click="pageup">
            <h1 class="navigation-page">Forum</h1>
            <h1 class="navigation-arrow">&#8249;</h1>
          </div>
          <div class="flex">
            <h1 class="title">{{title}}</h1>
            <h3 v-if="$store.state.loggedIn" class="link">
              <router-link :to="'/Forum/'+title+'/new'">add thread</router-link>
            </h3>
          </div>
          <div v-for="thread in threads" :key="thread.id">
            <div class="thread">
              <router-link :to="'/Forum/'+title+'/'+thread.id">
                <h2>{{thread.title}}</h2>
                <p>{{thread.description}}</p>
              </router-link>
            </div>
          </div>
        </div>
      </transition>
  </div>
</template>
<script>
import firebase from 'firebase/app'
import 'firebase/database'
export default {
  data(){
    return {
      threads: [],
      title: this.$route.params.subCategory
    }
  },
  methods: {
    pageup(){
        this.$router.replace('/Forum')
    }
  },
  created(){
    // axios.get(this.$store.state.api + '/api/forum/' + this.title).then((res) => {//TODO
    //   this.threads = res.data;
    //   console.log(this.threads)
    // })

    //get threads of current subforum
    let database = firebase.database();
    database.ref('/forum/threads/'+this.title.toLowerCase()).on('child_added', snapshot => {
      let newthread = snapshot.val()
      newthread.id = snapshot.key
      this.threads.push(newthread)
    })
  }
}
</script>
<style lang="stylus" scoped>
.title
  margin-bottom 3vh
.forum-navigation
  position relative
  cursor pointer
  text-align right
  width: fit-content
  transform translate(calc(-100% - 1em))
  transition all 0.8s
  max-height: 0
  &:hover
    transform translate(-10vw, 0)
    max-height: calc(5vw+50px)
    .navigation-page
      opacity 1
    .navigation-arrow
      opacity 0
.navigation-page
  padding-right: 0.3em
  display inline-block
  opacity 0
  transition opacity 0.5s
.navigation-arrow
  display inline-block
  color: #bdc3c7
  transition color,opacity 0.5s
</style>
