<template lang="html">
  <div class="wrapper">
    <h1 class="background-text">Wiki</h1>
    <!-- the Sections -->
    <transition name="fade" mode="out-in">
      <div :key="this.key" class="section">
        <div class="flex">
          <div>
            <transition name="smooth">
              <animated-input v-model="content.title" class="h1" placeholder="Title..." :readonly="!editmode"/>
              <!-- <h1 class="title" v-if="content.title">{{content.title}}</h1> -->
            </transition>
          </div>
          <div>
            <searchbar class="searchbar" v-model="searchText" placeholder="Search Wiki..."/>
          </div>
        </div>
        <wiki-search-results :results="searchResults" class="results">
          <transition-group name="smooth">
            <p v-if="content.description" key="content">{{content.description}}</p>
            <div v-for="section in this.content.sections" :key="section.id">
              <!-- <h2>{{section.title}}</h2> -->
              <!-- <p>{{section.content}}</p> -->
              <animated-input v-model="section.title" class="h2" placeholder="Section Title..." :readonly="!editmode"/>
              <resizeable-textarea v-model="section.content" :readonly="!editmode" placeholder="Content..."/>
            </div>
          </transition-group>
        </wiki-search-results>
      </div>
    </transition>
  </div>
</template>

<script>
import Searchbar from '../useful/Searchbar.vue'
import WikiSearchResults from './WikiSearchResults.vue'
import ResizeableTextarea from '../useful/ResizeableTextarea.vue'
import AnimatedInput from '../useful/AnimatedInput.vue'
//import axios from 'axios'

import firebase from 'firebase/app'
import 'firebase/database'

export default {
  components: {
    Searchbar,
    WikiSearchResults,
    AnimatedInput,
    ResizeableTextarea
  },
  data() {
    return {
      searchText: '',
      editmode: true,
      content: {}
    }
  },
  computed: {
    key() {
      var topic = this.$route.params.topic
      if (!topic)
        topic = 'wiki'
      return topic;
    },
    searchResults() {
      if (!this.searchText)
        return {
          displayDefault: true
        }
      else
        return this.$store.state.wikipages.filter((ele) => ele.toLowerCase().includes(this.searchText.toLowerCase())).sort()
    }
  },
  watch: {
    // call again the method if the route changes
    '$route': function() {
      this.fetchData()
    }
  },
  methods: {
    fetchData() {
      this.searchText = ''
      let page = this.key.toLowerCase()
      if(page == "")
        page = "wiki"
      firebase.database().ref("wiki/page/" + page).once("value", snapshot => {
        console.log(snapshot.val())
        this.content = snapshot.val();
        this.content.title = snapshot.key.charAt(0).toUpperCase() + snapshot.key.slice(1)
        var i = 0;
        for(var section of this.content.sections)
          section.id = i++;
        console.log(this.content)
      })
    }
  },
  created() {
    if(!this.$store.state.wikipages.length)
    firebase.database().ref("wiki/pages").once('value', snapshot => {
      let wikipages = []
      for(var key of Object.keys(snapshot.val())){
        wikipages.push(key.charAt(0).toUpperCase() + key.slice(1))
      }
      console.log(wikipages)
      //update store with wiki pages
      this.$store.commit('setwiki', wikipages)
    })
    this.fetchData()
  }
}
</script>

<style lang="stylus" scoped>
.flex
  align-items center

.smooth-enter-active
  transition all 1s ease
.smooth-enter
  transform translate(10px,0)
  opacity 0
</style>
