<template lang="html">
  <div class="wrapper">
    <h1 class="background-text">{{$store.state.player.name}}</h1>
    <div class="section">
      <div class="head">
        <img v-if="$store.state.player.uuid" :src="$store.state.head + $store.state.player.uuid" alt="Playerhead" >
      </div>
      <h2 class="logout" @click="logout" >Logout</h2>


      <h1 class="title">Profile</h1>
      <h2 class="name">{{$store.state.player.name}}</h2>
      <p class="uuid">{{$store.state.player.uuid}}</p>
      <h2 class="name">Stats</h2>
      <table class="stats">
        <tr v-for="(value, stat) in stats" :key="stat">
          <td>{{stat}}</td>
          <td>{{value}}</td>
        </tr>
      </table>
      <exchange-chart class="exchange"/>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import ExchangeChart from './charts/ExchangeChart.vue'
import firebase from 'firebase/app';
import 'firebase/auth'
export default {
  components: {
    ExchangeChart
  },
  data() {
    return {
      stats: {
        money: 154296321,
        token: 145,
        playtime: 103953,
        guild: 'Jayled'
      }
    }
  },
  methods:{
    logout(){
      firebase.auth().signOut().then(() => console.log("successfully logged out")).catch(() => console.log("there was an error loggin out"))
      this.$router.push('/')
      this.$store.commit('logout')
    }
  },
  computed: {
    headurl() {
      return this.$store.state.head  + this.uuid;
    },
    formattedStats() {
      return '';
    }
  },
  created() {
    if(this.$store.state.player.uuid)
    axios.get(this.$store.state.jayled +"/player/"+this.$store.state.player.uuid).then((res) => {
      if(res.data.success)
        this.stats = res.data.data;
    }).catch(() => console.log('Failed to load playerdata'));
  }
}
</script>

