export default {
  state: {
    loggedIn: false,
    player: {
      name: '',
      uuid: ''
    },
    //if components are visible
    nav: false,
    loadScreen: true,

    api: 'http://192.168.0.112:80',
    jayled: 'https://45.85.218.100:3000',
    head: 'https://minotar.net/helm/',

    wikipages: []
  },
  mutations: {
    login (state, player) {
      state.loggedIn = true;
      state.player = player;
    },
    logout (state) {
      state.loggedIn = false;
      state.player = {
        name: '',
        uuid: ''
      }
    },
    toggleNav(state){
      state.nav = !state.nav;
    },
    showNav(state){
      state.nav = true
    },
    hideNav(state){
      state.nav = false;
    },
    showLoadScreen(state){
      state.loadScreen = true
    },
    hideLoadScreen(state){
      state.loadScreen = false
    },
    setwiki(state, wikipages){
      state.wikipages = wikipages;
    }
  }
}
