<template>
<div>
  <!-- LoadScreen and Navigation are always rendered -->
  <load-screen />
  <navigation />
  <transition name="fade" mode="out-in">
    <router-view></router-view>
  </transition>
</div>
</template>

<script>
import LoadScreen from './components/LoadScreen.vue'
import Navigation from './components/Navigation.vue'
import axios      from 'axios'
import Hammer from 'hammerjs'
import firebase from 'firebase/app';
import 'firebase/auth'
export default {
  name: 'App',
  components: {
    LoadScreen,
    Navigation,
  },
  data(){
    return {
      firestChange: true //value to track if user was already logged in --> without user would be automatically logged in again after loggin out.
    }
  },
  methods: {
    async relog(user){
        if (user) {
          //user is still logged in
          //get playername from uuid using the mojand api
          //and change state to loggedIn
          var res = await axios.get(this.$store.state.jayled + "/name/" + user.uid);
          var player = {
            name: res.data.name,
            uuid: user.uid
          }
          this.$store.commit('login', player)
        } else if(this.firstChange){
          //user is not logged in
          //do ip login --> logs the player in if he is currently playing on the server
          axios.post(this.$store.state.jayled + '/login',{}).then((res) => {
            console.log(res.data);
            if(res.data.success){
              firebase.auth().signInWithCustomToken(res.data.token).catch(() => console.log("Firebase login failed"))
              this.$store.commit('login', res.data.player)
            }
          })
        }
        this.firstChange = false;
    }
  },
  async created(){
    var touch = new Hammer(window)//works on every page
    touch.get('pan').set({
      threshold: 20
    })
    touch.on('panleft', (e) => {
      let acceptedAngle = 25;
      if(e.angle < -180+acceptedAngle || e.angle > 180-acceptedAngle)//real left pan!
        this.$store.commit('showNav')
    })

    firebase.auth().onAuthStateChanged(this.relog);
  }
}
</script>
