<template lang="html">
  <div class="wrapper">
    <h1 class="background-text">Forum</h1>
    <div class="section">
      <!-- Arrow linking to category above -->
      <div class="forum-navigation" @click="pageup">
        <h1 class="navigation-page">{{this.subcatName}}</h1>
        <h1 class="navigation-arrow">&#8249;</h1>
      </div>

      <!-- thread title and edit-button-->
      <div class="flex">
        <animated-input v-model="data.thread.title" class="h1" placeholder="Title..." :readonly="!editmode.includes(0)"/>
        <div v-if="data.thread.creator && $store.state.player.uuid == data.thread.creator.uuid">
          <h3 v-if="!editmode.includes(0)" @click="editmode.push(0)" class="link">edit</h3>
          <h3 v-else @click="updateThread()" class="link">save</h3>
        </div>
      </div>

      <!-- Description of the thread (initial post) -->
      <div class="post" key='thread-description' v-if="data.thread.creator">
        <h2 class="creator">{{data.thread.creator.name}}</h2>
        <img class="head" :src="$store.state.head + data.thread.creator.uuid" alt="Playerhead">
        <div class="date">{{new Date(Number(data.thread.id)).toLocaleString()}}</div>
        <resizeable-textarea class="content" v-model="data.thread.description" :readonly="!editmode.includes(0)" placeholder="Content..."/>
        <small v-show="data.thread.description.length == 0" class="red">Post cannot be empty</small>
      </div>

      <transition-group name="fade-list" mode="out-in">
        <!-- All posts inside of this thread -->
        <div v-for="(post, id) of data.posts" class="post" :key="post.id">
          <h2 class="creator">{{post.creator.name}}</h2>
          <img class="head" v-if="post.creator.uuid" :src="$store.state.head + post.creator.uuid" alt="Playerhead">
          <div class="date">{{new Date(Number(id)).toLocaleString()}}</div>
          <resizeable-textarea class="content" v-model="post.content" :readonly="!editmode.includes(id)" placeholder="Content..."/>
          <small v-show="post.content.length == 0" class="red">Post cannot be empty</small>
          <div v-if="$store.state.player.uuid == post.creator.uuid" class="buttons">
            <p v-if="!editmode.includes(id)" @click="editmode.push(id)" class="link post-edit-button">edit</p>
            <div v-else>
              <p @click="deletePost(id)" class="link post-save-button">delete</p>
              <p @click="updatePost(id)" class="link post-save-button">save</p>
            </div>
          </div>
        </div>
      </transition-group>

        <!-- Empty placeholder to create new post -->
        <div v-if="threadId != 'new' && $store.state.loggedIn" class="post" key='new-post'>
          <h2 class="creator">{{this.$store.state.player.name}}</h2>
          <img class="head" :src="$store.state.head + $store.state.player.uuid" alt="">
          <div class="date">{{new Date().toLocaleString()}}</div>
          <resizeable-textarea class="content" v-model="newAnswer" placeholder="Add answer..."/>
          <div class="buttons">
            <p @click="addPost" class="link post-send-button">send</p>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
// import axios from 'axios'
import ResizeableTextarea from '../useful/ResizeableTextarea.vue'
import AnimatedInput from '../useful/AnimatedInput.vue'
import firebase from 'firebase/app'
import 'firebase/database'
import Vue from 'vue'

export default {
  components: {
    ResizeableTextarea,
    AnimatedInput
  },
  data(){
    return {
      threadId: this.$route.params.threadId,
      subcatName: this.$route.params.subCategory,
      editmode:  [],
      data: {
        thread: {},
        posts: {}
      },
      newAnswer: ''
    }
  },
  methods: {
    updatePost(id){
      firebase.database().ref('forum/posts/').child(this.subcatName.toLowerCase()).child(this.threadId).child(id).update(
        this.data.posts[id]
      ).then(() => {
        this.editmode.splice(this.editmode.indexOf(id),1);
      }).catch(() => {
        console.log("some error occurred while updating the post inside the firebase")
      })

      // axios.post(this.$store.state.api + '/api/forum/' + this.subcatName + '/' + this.threadId, changedPost).then((res) => {
      //   this.editmode.splice(this.editmode.indexOf(id),1);//toggle editmode
      //   if(this.threadId == 'new'){//new thread created
      //     this.threadId = res.data.Thread.Id
      //     this.$router.replace('/Forum/'+this.subcatName+'/'+this.threadId)//update url
      //   }
      //   if(id == 0){//first post
      //     this.data.Posts.Id = res.data.Post.Id;//update id
      //   }
      // })
    },
    addPost(){
      if(this.newAnswer.length == 0){ //do nothing
        console.log("Answer cant be empty");
        return;
      }
      let database = firebase.database()
      database.ref('forum/posts/'+this.subcatName.toLowerCase()+'/'+this.threadId+'/'+ Date.now()).set({
        content: this.newAnswer,
        creator: this.$store.state.player
      }).then(() => {
        this.newAnswer = ''
      }).catch(() => console.log("Some error occurred writing this post to the firebase"))
    },
    deletePost(id){
      firebase.database().ref('forum/posts/').child(this.subcatName.toLowerCase()).child(this.threadId).child(id).remove().then(() => {
        this.editmode.splice(this.editmode.indexOf(id),1);
        delete this.data.posts[id];
      }).catch(() => {
        console.log("some error occurred while deleting the post inside the firebase")
      })
    },
    updateThread(){
      let data = this.data.thread;
      if(this.threadId == 'new'){//create new thread
        let time = Date.now();
        delete data.id;
        firebase.database().ref('forum/threads/'+this.subcatName.toLowerCase()+'/'+time).set(data)
        this.threadId = time;
        this.$router.replace('/Forum/'+this.subcatName+'/'+this.threadId)//update url
      } else {
        firebase.database().ref('forum/threads/'+this.subcatName.toLowerCase()+'/'+this.threadId).update(data)
      }
      this.editmode.splice(this.editmode.indexOf(0),1);
    },
    pageup(){
      this.$router.replace('/Forum/'+this.subcatName)
    }
  },
  created(){
    //load posts of thread from firebase
    if(this.threadId == 'new'){
      //creating new thread
      this.editmode.push(0);
      this.data.thread = {
        id: 'new',
        title: '',
        creator: this.$store.state.player,
        description: ''
      }
      // this.data.Posts = [{
      //   Content: '',
      //   Id: 'new',
      //   Creator: this.$store.state.player.uuid,
      //   CreatorName: this.$store.state.player.name,
      //   Date: Date.now()
      // }]
    } else {
      let database = firebase.database()
      //load thread name and description
      database.ref('forum/threads/'+this.subcatName.toLowerCase()+'/'+this.threadId).once('value', snapshot => {
        let thread = snapshot.val()
        thread.id = snapshot.key //UTC time of creation
        this.data.thread = thread
        console.log(thread)
      })

      // load posts of current thread
      database.ref('forum/posts/'+this.subcatName.toLowerCase()+'/'+this.threadId).on('child_added', snapshot => {
        let post = snapshot.val()
        post.id = snapshot.key;
        Vue.set(this.data.posts, snapshot.key, post);//needed in order to trigger refresh on the v-for element
      })
    }

    // console.log(this.thread)
    // if(this.threadId == 'new') {
    //   this.editmode.push(0);
    //   this.data.Thread = {
    //     Title: '',
    //     Creator: this.$store.state.player.uuid,
    //   }
    //   this.data.Posts = [{
    //     Content: '',
    //     Id: 'new',
    //     Creator: this.$store.state.player.uuid,
    //     CreatorName: this.$store.state.player.name,
    //     Date: Date.now()
    //   }]
    // } else {
    //   axios.get(this.$store.state.api + '/api/forum/' + this.subcatName + '/' + this.threadId).then((res) => {//TODO
    //     this.data = res.data;
    //
    //     console.log("Thread");
    //     console.log(res.data);
    //
    //     this.Posts = [{
    //       Content: '',
    //       Id: 0,
    //       Creator: this.$store.state.player.uuid,
    //       Date: Date.now()
    //     }]
    //   })
    // }
  }
}
</script>

