  margin-left 1em
.head
  z-index: -1
  position: absolute
  height: 8vh
  top: 0
  left: -4vh
  opacity: 0.5
.creator
  margin-bottom 0
.content, .post small
  margin-left: 5%
small
  position: absolute
  // transform: translate(0,-100%)
.date
  position: absolute
  top: 0
  right: 0
.red
  color: #e74c3c
//------------------------ nav
.forum-navigation
  position relative
  cursor pointer
  text-align right
  width: fit-content
  transform translate(calc(-100% - 1em))
