<template lang="html">
  <div class="chart">
    <div class="chart-header">
      <h2>Exchange Market</h2>
      <div class="chart-toolbar">
        <!-- Make time smaller .. like weeks and days ... maybe hours -->
        <!-- <button type="button" class="btn btn-outline-primary" @click="updateData('one_month')" :class="{active: selection==='one_month'}">1M</button>
        <button type="button" class="btn btn-outline-primary" @click="updateData('one_month')" :class="{active: selection==='on'}">1M</button> -->
        <button type="button" class="button" @click="zoom('live')" :class="{active: selection==='live'}">Live</button>
        <button type="button" class="button" @click="zoom('one_hour')" :class="{active: selection==='one_hour'}">1H</button>
        <button type="button" class="button" @click="zoom('one_day')" :class="{active: selection==='one_day'}">1D</button>
        <button type="button" class="button" @click="zoom('one_week')" :class="{active: selection==='one_week'}">1W</button>
        <button type="button" class="button" @click="zoom('one_month')" :class="{active: selection==='one_month'}">1M</button>
        <button type="button" class="button" @click="zoom('all')" :class="{active: selection==='all'}">ALL</button>
      </div>
    </div>
    <div class="chart-timeline">
      <apexchart type="area" height="100%" ref="chart" :options="chartOptions" :series="series"></apexchart>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase/app'
import 'firebase/database'

export default {
  data() {
    return {
      series: [{
        name: "1 token",
        data: [[123,23]]
      }],
      chartOptions: {
        grid: {
          show: true,
          padding: {
            // left: 0,
            right: 0
          }
        },
        chart: {
          id: 'area-datetime',
          type: 'area',
          // height: '100%',
          zoom: {
            autoScaleYaxis: true
          },
          toolbar: {
            show: false
          },
          events: {
            beforeZoom: this.safeScroll,
            zoomed: this.setScroll
          }
        },
        colors: ['#2ecc71'],
        dataLabels: {
          enabled: false
        },
        markers: {
          size: 0,
          style: 'hollow',
        },
        xaxis: {
          type: 'datetime',
        },
        yaxis: {
          labels: {
            formatter(val){
              return Math.round(val) + ' $'
            },
            align: 'right'
          }
        },
        tooltip: {
          x: {
            format: 'dd MMM yyyy HH:mm:ss',
            show: false
          },
          marker: {
            show: false
          }
        },
        fill: {
          type: 'gradient',
          gradient: {
            shadeIntensity: 1,
            opacityFrom: 0.7,
            opacityTo: 0.9,
            stops: [0, 100]
          }
        },
      },
      selection: 'one_month',
      scrollPos: 0
    }//return end
  },//data end
  methods: {
    safeScroll(){//beforeZoom
      this.selection = ''
      this.scrollPos = document.documentElement.scrollTop || document.body.scrollTop;
    },
    setScroll(){//afterZoom
      document.documentElement.scrollTop = document.body.scrollTop = this.scrollPos;
    },
    zoom(timeline) {
      let now = Date.now()
      switch (timeline) {
        case 'live': //10minutes
          this.$refs.chart.zoomX(now - 600000, now)
          break
        case 'one_hour':
          this.$refs.chart.zoomX(now - 3.6e+6, now)
          break
        case 'one_day':
          this.$refs.chart.zoomX(now - 8.64e+7, now)
          break
        case 'one_week':
          this.$refs.chart.zoomX(now - 6.048e+8, now)
          break
        case 'one_month':
          this.$refs.chart.zoomX(now - 2.628e+9, now)
          break
        case 'all':
          this.$refs.chart.zoomX(new Date('1 Mar 2020').getTime(), now)
          break
        default:
      }
      this.selection = timeline
    },
    addData(data){
      this.$refs.chart.appendData([{
        data: data
      }])
      if(this.selection == 'live') this.zoom('live')
    },
    setData(data){
      if(this.$refs.chart)
        this.$refs.chart.updateSeries([{
          name: "1 token",
          data: data
        }]);
      // this.zoom('one_month')
    }
  },
  created() {
    let database = firebase.database();

    //get all market data
    database.ref('/market').once('value', snapshot => {
      console.log(snapshot.val())

      let deals = snapshot.val();

      delete deals.new;

      let data = []
      for (var [date, deal] of Object.entries(deals)) {
        data.push([Number(date), deal.MpT]);
      }
      console.log(data)
      this.setData(data)
    })

    //realtime update event
    database.ref('/market').on('child_changed', snapshot => {
      if(snapshot.key == 'new'){
        //the 'new' child is modified everytime the server adds new data.
        //this is no standard

        let val = snapshot.val();
        this.addData([[Number(val.Date), val.MpT]]);
      }
    })
  }
}
</script>

<style lang="stylus" scoped>
.chart-header
  flex: 0 1 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

.chart-toolbar
  display flex
  align-items: center
  justify-content: space-between;

.chart
  display: flex
  flex-flow: column

.chart-timeline
  flex: 1 1 auto

button
  margin: 0 5px
</style>
