<template lang="html">
    <div class="wrapper">
      <h1 class="background-text">Login</h1>
      <div class="section">
        <h1>Login</h1>
        <!-- <label for="username">Username</label> -->
        <div class="input-section">
          <animated-input @keyup.enter="login" class="login-input h2" v-model="username" id="login-username" aria-describedby="login-userHelp" placeholder="Username"/>
          <small id="login-userHelp" class="form-text">Please use your minecraft username</small>
          <small class="form-text error" v-if="usererror">{{usererror}}</small>
        </div>
        <div class="input-section">
          <animated-input @keyup.enter="login" class="login-input h2" v-model="password" type="password" aria-describedby="login-passHelp" placeholder="Password"/>
          <small id="login-passHelp" class="form-text">DO NOT enter your minecraft password, you need a JayledMc password</small>
          <small class="form-text error" v-if="passerror">{{passerror}}</small>
        </div>
        <button @click="login" class="button font2">Login</button>
      </div>
    </div>
</template>

<script>
import axios from 'axios'
import AnimatedInput from './useful/AnimatedInput.vue'
import firebase from 'firebase/app';
import 'firebase/auth'
export default {
  components: {
    AnimatedInput
  },
  data(){
    return {
      username: "",
      password : "",
      usererror: "",
      passerror: ""
    }
  },
  methods: {
    login(){
      // axios.post("http://localhost:3000/login", {
      axios.post(this.$store.state.jayled + '/login', {
        username: this.username,
        password: this.password
      }).then((res) => {
        //callback
        console.log(res.data);
        if(res.data.success){
          firebase.auth().signInWithCustomToken(res.data.token).catch(() => console.log("Firebase login failed"))
          this.$store.commit('login', res.data.player)
          this.$router.push('/Profile')
        }
      }).catch((err) => {
        console.log('login failed')
        console.log(err)})
    }
  }
}
</script>
<style lang="stylus" scoped>
.input-section
  margin-bottom: 2vh
.section
  font-size: 150%
h1
  margin-bottom: .5em
.login-input
  margin-bottom: 5px
  padding: 0.2em 0em
.form-text
  margin-bottom 20px
.error
  color: red
</style>
