<template lang="html">
  <div class="input-wrapper" :class="{filled: filled}">
    <textarea class="input" @input="input" :value="value" :readonly="readonly"></textarea>
    <label class="label" for="area">
      <span class="placeholder">{{placeholder}}</span>
    </label>
  </div>
</template>

<script>
export default {
  props: ['value','placeholder','readonly'],
  methods: {
    resizeTextarea(target) {
      let scrollPos = document.documentElement.scrollTop || document.body.scrollTop;

      let originalHeight = Number(target.style.height.slice(0,-2));
      target.style.height = '0px'

      let diff = target.scrollHeight - originalHeight;
      target.style.height = (target.scrollHeight) + 'px'

      document.documentElement.scrollTop = document.body.scrollTop = scrollPos+diff;
    },
    input(event){
      this.$emit('input', event.target.value);
      this.resizeTextarea(event.target);
    }
  },
  computed: {
    filled(){
      return this.value.length != 0;
    }
  },
  mounted() {
      this.resizeTextarea(this.$el.firstChild)
  }
}
</script>
<style lang="stylus" scoped>
.input-wrapper
  position relative
.input
  display block
  margin 0
  width 100%
  background transparent
  color: inherit;
.input:focus + .label .placeholder,
.filled .placeholder
  opacity: 0
  transform: translate(20px)
.input[readonly] + .label::after,
.input[readonly] + .label::before
  opacity: 0
.label
  display block
  margin: 0
  position: absolute
  top: 0
  left: 0
  width: 100%
  height: 100%
  padding: inherit;
  pointer-events: none
  &::after
    content: '';
    z-index: -1;
    position: absolute;
    left: 0;
    bottom: 0
    width: 100%;
    height: 100%;
    max-height: 100%
    background: rgba(0,0,0,0.1);
    transition: all 0.3s;
  &::before
    content ''
    z-index 0
    position: absolute;
    bottom 0
    width 100%
    max-width: 0
    height 4px
    background-color rgba(0,0,0,0.4)
    transition max-width 0.3s;
.placeholder
  display: inline-block
  transition: all 0.3s
  color: inherit

.input:focus + .label::after,
.filled .label::after
  max-height: 4px

.input:focus + .label::before
  max-width: 100%
</style>
