<template lang="html">
  <div>
    <!-- Menu icon -->
    <div class="menu-toggle" :class="{visible: !$store.state.nav}" @click="$store.commit('toggleNav')">
      <p>Menu</p>
    </div>
    <div class="about-toggle">
      <router-link to="/About">
        <p>About</p>
      </router-link>
    </div>
    <nav :class="{visible: $store.state.nav}">

      <!-- The nav itself -->
      <ul class="menu">
        <li @click="$store.commit('hideNav')" class="site" v-for="(link,site) of sites" :key="site" >
          <router-link :to="'/'+link" class="font2">
            {{site}}
          </router-link>
        </li>
        <!-- login/account button -->
        <li v-if="$store.state.loggedIn" @click="$store.commit('hideNav')" class="site">
          <router-link to="/Profile" class="font2">
            Profile
          </router-link>
        </li>
        <li v-else @click="$store.commit('hideNav')" class="site">
          <router-link to="/Login" class="font2">
            Login
          </router-link>
        </li>
      </ul>
    </nav>
    <!-- The blocker is not inside the nav -->
    <transition>
      <div @click="$store.commit('hideNav')" v-show="$store.state.nav" class="blocker">
      </div>
    </transition>
  </div>
</template>

<script>
import Hammer from 'hammerjs'
export default {
  data() {
    return {
      sites: {
        Home: "Home",
        Forum: "Forum",
        Wiki: "Wiki",
        Store: "Store",
        Vote: "Vote",
        Rules: "Rules"
      }
    }
  },
  methods: {
    showLogin() {
      this.loginVisible = true;
    }
  },
  mounted() {
    var touch = new Hammer(this.$el)
    touch.get('pan').set({
      threshold: 5
    })
    touch.on('panright', (e) => {
      let acceptedAngle = 25;
      if(e.angle > -acceptedAngle && e.angle < acceptedAngle)//real right pan!
        this.$store.commit('hideNav')
    })
  }
}
</script>

